import {
  Button,
  Grid,
  Menu,
  IconButton,
  InputAdornment,
  TextField,
  Typography,
} from "@mui/material";
import { useState } from "react";
import HelpCenterIcon from "@mui/icons-material/HelpCenter";

function EventRegister(props) {
  const [eventno, setEventNo] = useState("");
  const [eventname, setEventName] = useState("");
  const [tranid, setTranId] = useState("");
  const [membername, setMemberName] = useState("");
  const [memberno, setMemberNo] = useState("");
  const [date, setDate] = useState("");
  const [myRoot, setMyRoot] = useState(null);
  const open = Boolean(myRoot);
  const [myRoot1, setMyRoot1] = useState(null);
  const open1 = Boolean(myRoot1);
  return (
    <div>
      <Grid
        container
        spacing={2}
        direction="column"
        alignItems="center"
        justifyContent="center"
      >
        <Grid item xs={6}>
          <TextField
            required
            label="Event Name"
            onChange={(event) => {
              setEventName(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            label="Event Number"
            type="number"
            onChange={(event) => {
              setEventNo(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            label="Member Name"
            onChange={(event) => {
              setMemberName(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            type={"number"}
            label="Member Phone Number"
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    onClick={(event) => {
                      setMyRoot1(event.currentTarget);
                    }}
                  >
                    <HelpCenterIcon />
                  </IconButton>
                  <Menu
                    anchorEl={myRoot1}
                    open={open1}
                    onClose={() => {
                      setMyRoot1(null);
                    }}
                  >
                    <Typography sx={{ mr: 2 }}>
                      আপনার নিজের টাকা পাঠানো বিকাশ অথবা নগদ অথবা রকেট নাম্বার
                      দিন
                    </Typography>
                  </Menu>
                </InputAdornment>
              ),
            }}
            onChange={(event) => {
              setMemberNo(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            onChange={(event) => {
              setTranId(event.currentTarget.value);
            }}
            label="Transaction ID"
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    onClick={(event) => {
                      setMyRoot(event.currentTarget);
                    }}
                  >
                    <HelpCenterIcon />
                  </IconButton>
                  <Menu
                    anchorEl={myRoot}
                    open={open}
                    onClose={() => {
                      setMyRoot(null);
                    }}
                  >
                    <Typography sx={{ mr: 2 }}>
                      Send money - যেকোন একটি নাম্বারে টাকা পাঠিয়ে Transaction
                      id copy করে নিন
                      <br />
                      bKash: 01521217545
                      <br />
                      Nagad: 01742942755
                      <br />
                      Rocket: 01408085909
                    </Typography>
                  </Menu>
                </InputAdornment>
              ),
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>

        <Grid item xs={6}>
          <TextField
            required
            onChange={(event) => {
              setDate(event.currentTarget.value);
            }}
            type="date"
            label="Date of Event"
            InputLabelProps={{
              shrink: true,
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>

        <Grid item xs={6}>
          <Button
            disabled={
              eventname == "" ||
              eventno == "" ||
              membername == "" ||
              tranid == "" ||
              date == ""
            }
            onClick={() => {
              var myHeaders = new Headers();
              myHeaders.append(
                "Content-Type",
                "application/x-www-form-urlencoded"
              );

              var urlencoded = new URLSearchParams();
              urlencoded.append("ename", eventname);
              urlencoded.append("evnum", eventno);
              urlencoded.append("cardno", tranid);
              urlencoded.append("edate", date);
              urlencoded.append("cname", membername);
              urlencoded.append("mnum", memberno);

              var requestOptions = {
                method: "POST",
                headers: myHeaders,
                body: urlencoded,
                redirect: "follow",
              };

              fetch("http://localhost/eventregistration", requestOptions).then(
                (response) => {
                  if (response.status == 200) {
                    console.log("OK");
                    props.goaway(false);
                  }
                }
              );
            }}
            sx={{ width: "30vw" }}
            variant="contained"
          >
            Proceed
          </Button>
        </Grid>
      </Grid>
    </div>
  );
}

export default EventRegister;
