import {
  Dialog,
  DialogContent,
  DialogTitle,
  Grid,
  IconButton,
  Typography,
  Card,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import CloseIcon from "@mui/icons-material/Close";

function Transaction(props) {
  const [data, setData] = useState([]);
  useEffect(() => {
    fetch("http://localhost/tranl")
      .then((response) => response.json())
      .then((result) => {
        setData(result);
      });
  }, []);
  return (
    <Dialog open={props.tran[0]}>
      <DialogTitle>
        <IconButton
          sx={{
            position: "absolute",
            right: 8,
            top: 8,
            mr: 2,
          }}
          onClick={() => {
            props.tran[1](false);
          }}
        >
          <CloseIcon />
        </IconButton>
        <Typography variant="h5">Transaction List</Typography>
      </DialogTitle>
      <DialogContent>
        <Grid container spacing={2}>
          {console.log(data)}
          {data.map((item) => {
            return (
              <Grid item xs={12}>
                <Card sx={{ padding: 5 }}>
                  <Typography variant="h6">{item.eventname}</Typography>
                  <Typography>{"Fee: ৳" + item.fee}</Typography>
                  <Typography>{"Event No: " + item.eventno}</Typography>
                  <Typography>{"Trx ID: " + item.transactionid}</Typography>
                </Card>
              </Grid>
            );
          })}
        </Grid>
      </DialogContent>
    </Dialog>
  );
}

export default Transaction;
