import {
  Box,
  Pagination,
  Typography,
  Grid,
  Toolbar,
  Card,
  CardContent,
  IconButton,
  CardActions,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
} from "@mui/material";
import EventRegister from "./EventRegister";
import EventCreate from "./EventCreate";
import "./style/Home.css";
import { useEffect, useState } from "react";
import CloseIcon from "@mui/icons-material/Close";

const defu = [
  {
    eventno: "loading",
    eventname: "loading",
    coordinatorname: "loading",
    coordinatorno: "loading",
    fee: "loading",
    venue: "loading ",
    date: "loading",
    type: "loading",
  },
];

export default function Home(props) {
  const [eventData, setData] = useState(defu);
  const [randomEvent, setRand] = useState(defu);
  const [open, setopen] = useState(false);

  let isAdmin = props.isAdmin;

  useEffect(() => {
    fetch("http://localhost/loadeventlist")
      .then((response) => response.json())
      .then((result) => {
        setData(result);
        setRand(result[Math.floor(Math.random() * result.length)]);
      })
      .catch((error) => console.log("error", error));
  }, []);
  return (
    <Box>
      <Dialog open={open}>
        <DialogTitle>
          <IconButton
            sx={{
              position: "absolute",
              right: 8,
              top: 8,
              mr: 2,
            }}
            onClick={() => {
              setopen(false);
            }}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant="h5"> Register Event</Typography>
        </DialogTitle>
        <DialogContent sx={{ p: 2 }}>
          <br />
          <br />
          <EventRegister goaway={setopen} />
          <br />
          <br />
        </DialogContent>
      </Dialog>

      <Dialog open={props.ceopen}>
        <DialogTitle>
          <IconButton
            sx={{
              position: "absolute",
              right: 8,
              top: 8,
              mr: 2,
            }}
            onClick={() => {
              props.ceopens(false);
            }}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant="h5">Create Event</Typography>
        </DialogTitle>
        <DialogContent sx={{ p: 2 }}>
          <br />
          <br />
          <EventCreate update={[setData, props.ceopens]} />
          <br />
          <br />
        </DialogContent>
      </Dialog>

      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Box className="back">
            <div
              style={{
                backgroundColor: "#ffffff55",
                padding: "20px",
                left: "35%",
                top: "-10%",
                position: "relative",
              }}
            >
              <Typography variant="h4">{randomEvent.eventname}</Typography>
              <Typography variant="h5">
                {"Registration fee: ৳" + randomEvent.fee}
              </Typography>
              <Typography variant="h5">
                {"Place: " + randomEvent.venue}
              </Typography>
              <Typography variant="h5">
                {"Date: " + randomEvent.date}
              </Typography>
              <Typography variant="h6">
                {"Event No.: " + randomEvent.eventno}
              </Typography>
              <Typography variant="h6">
                {"Event Co-Ordinator: " + randomEvent.coordinatorname}
              </Typography>
              <Typography variant="h6">
                {"Co-Ordinator Phone No.: " + randomEvent.coordinatorno}
              </Typography>
              {props.isAuth[0] ? (
                !isAdmin && (
                  <Button
                    onClick={() => {
                      setopen(true);
                    }}
                    variant="contained"
                  >
                    Register
                  </Button>
                )
              ) : (
                <Button
                  onClick={() => {
                    props.isAuth[1](true);
                  }}
                >
                  Log in to see actions
                </Button>
              )}
            </div>
          </Box>
        </Grid>
        <Grid item xs={5} />
        <Grid item xs={4}>
          <Pagination
            onChange={(event, page) => {
              console.log(page);
              setRand(eventData[eventData.length - page]);
            }}
            count={7}
            color="primary"
          />
        </Grid>
        <Grid item xs={3} />
      </Grid>

      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Toolbar id="motiv-section" />
          <Toolbar />
          <Typography
            variant="h4"
            style={{
              backgroundColor: "#0a0a0a",
              width: "fit-content",
              color: "#ffffff",
            }}
          >
            Motivational Speaker
          </Typography>
          <hr />
        </Grid>
        {eventData.map((item) => {
          if (item.type === "Motivational Speaker") {
            return (
              <Grid item xs={3}>
                <Card>
                  <CardContent>
                    <Typography variant="h4">{item.eventname}</Typography>
                    <Typography variant="h5">
                      {"Registration fee: ৳" + item.fee}
                    </Typography>
                    <Typography variant="h5">
                      {"Place: " + item.venue}
                    </Typography>
                    <Typography variant="h5">{"Date: " + item.date}</Typography>
                    <Typography variant="h6">
                      {"Event No.: " + item.eventno}
                    </Typography>
                    <Typography variant="h6">
                      {"Event Co-Ordinator: " + item.coordinatorname}
                    </Typography>
                    <Typography variant="h6">
                      {"Co-Ordinator Phone No.: " + item.coordinatorno}
                    </Typography>
                  </CardContent>
                  <CardActions>
                    {props.isAuth[0] ? (
                      !isAdmin && (
                        <Button
                          onClick={() => {
                            setopen(true);
                          }}
                          color="error"
                          variant="contained"
                        >
                          Register
                        </Button>
                      )
                    ) : (
                      <Button
                        onClick={() => {
                          props.isAuth[1](true);
                        }}
                        color="error"
                      >
                        Log in to see actions
                      </Button>
                    )}
                  </CardActions>
                </Card>
              </Grid>
            );
          }
        })}

        <Grid item xs={12}></Grid>
        <Grid item xs={12}></Grid>
        <Grid item xs={12}></Grid>
        <Grid item xs={12}>
          <Toolbar id="play-section" />
          <Toolbar />
          <Typography
            variant="h4"
            style={{
              backgroundColor: "#0a0a0a",
              width: "fit-content",
              color: "#ffffff",
            }}
          >
            Playback Singer
          </Typography>
          <hr />
        </Grid>
        {eventData.map((item) => {
          if (item.type === "Playback Singer") {
            return (
              <Grid item xs={3}>
                <Card>
                  <CardContent>
                    <Typography variant="h4">{item.eventname}</Typography>
                    <Typography variant="h5">
                      {"Registration fee: ৳" + item.fee}
                    </Typography>
                    <Typography variant="h5">
                      {"Place: " + item.venue}
                    </Typography>
                    <Typography variant="h5">{"Date: " + item.date}</Typography>
                    <Typography variant="h6">
                      {"Event No.: " + item.eventno}
                    </Typography>
                    <Typography variant="h6">
                      {"Event Co-Ordinator: " + item.coordinatorname}
                    </Typography>
                    <Typography variant="h6">
                      {"Co-Ordinator Phone No.: " + item.coordinatorno}
                    </Typography>
                  </CardContent>
                  <CardActions>
                    {props.isAuth[0] ? (
                      !isAdmin && (
                        <Button
                          onClick={() => {
                            setopen(true);
                          }}
                          variant="contained"
                          color="error"
                        >
                          Register
                        </Button>
                      )
                    ) : (
                      <Button
                        onClick={() => {
                          props.isAuth[1](true);
                        }}
                        color="error"
                      >
                        Log in to see actions
                      </Button>
                    )}
                  </CardActions>
                </Card>
              </Grid>
            );
          }
        })}

        <Grid item xs={12}></Grid>
        <Grid item xs={12}></Grid>
        <Grid item xs={12}></Grid>
        <Grid item xs={12}>
          <Toolbar id="model-section" />
          <Toolbar />
          <Typography
            variant="h4"
            style={{
              backgroundColor: "#0a0a0a",
              width: "fit-content",
              color: "#ffffff",
            }}
          >
            Model
          </Typography>
          <hr />
        </Grid>
        {eventData.map((item) => {
          if (item.type === "Model") {
            return (
              <Grid item xs={3}>
                <Card>
                  <CardContent>
                    <Typography variant="h4">{item.eventname}</Typography>
                    <Typography variant="h5">
                      {"Registration fee: ৳" + item.fee}
                    </Typography>
                    <Typography variant="h5">
                      {"Place: " + item.venue}
                    </Typography>
                    <Typography variant="h5">{"Date: " + item.date}</Typography>
                    <Typography variant="h6">
                      {"Event No.: " + item.eventno}
                    </Typography>
                    <Typography variant="h6">
                      {"Event Co-Ordinator: " + item.coordinatorname}
                    </Typography>
                    <Typography variant="h6">
                      {"Co-Ordinator Phone No.: " + item.coordinatorno}
                    </Typography>
                  </CardContent>
                  <CardActions>
                    {props.isAuth[0] ? (
                      !isAdmin && (
                        <Button
                          onClick={() => {
                            setopen(true);
                          }}
                          variant="contained"
                          color="error"
                        >
                          Register
                        </Button>
                      )
                    ) : (
                      <Button
                        onClick={() => {
                          props.isAuth[1](true);
                        }}
                        color="error"
                      >
                        Log in to see actions
                      </Button>
                    )}
                  </CardActions>
                </Card>
              </Grid>
            );
          }
        })}
      </Grid>
      <Toolbar />
    </Box>
  );
}
