import { Button, Grid, Stack, TextField, Typography } from "@mui/material";
import { useState } from "react";

function CreateAccount(props) {
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmpassword, setConfirmPassword] = useState("");
  const [text, setText] = useState("");
  const [error, setError] = useState(false);
  const [cerror, setCerror] = useState(false);
  const [eerror, setEerror] = useState(false);
  const passwordRegex =
    /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,1600}$/;
  const emailRegex =
    /^(([^<>()[\]\\.,;:\s@$"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return (
    <div>
      <Grid
        container
        spacing={2}
        direction="column"
        alignItems="center"
        justifyContent="center"
        style={{ minHeight: "100vh" }}
      >
        <Grid item xs={6}>
          <TextField
            required
            error={eerror}
            helperText={eerror ? "Invalid email" : ""}
            label="Email Address"
            onChange={(event) => {
              setEmail(event.currentTarget.value);
              setEerror(!emailRegex.test(event.currentTarget.value));
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            label="Full Name"
            onChange={(event) => {
              setName(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            label="User Name"
            onChange={(event) => {
              setUsername(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            error={error}
            helperText={
              error
                ? "Use at least 7 alpha numeric characters or symbols !"
                : ""
            }
            onChange={(event) => {
              setPassword(event.currentTarget.value);
              setError(!passwordRegex.test(event.currentTarget.value));
            }}
            label="Password"
            type="password"
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            error={cerror}
            helperText={cerror ? "Passwords Do Not Match !" : ""}
            onChange={(event) => {
              setConfirmPassword(event.currentTarget.value);
              //   console.log(password == event.currentTarget.value);
              setCerror(!(password == event.currentTarget.value));
            }}
            label="Confirm Password"
            type="password"
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <Button
            disabled={
              error ||
              cerror ||
              eerror ||
              email == "" ||
              username == "" ||
              password == ""
            }
            onClick={() => {
              var myHeaders = new Headers();
              myHeaders.append(
                "Content-Type",
                "application/x-www-form-urlencoded"
              );

              var urlencoded = new URLSearchParams();
              urlencoded.append("email", email);
              urlencoded.append("pass", password);
              urlencoded.append("name", name);
              urlencoded.append("username", username);

              var requestOptions = {
                method: "POST",
                headers: myHeaders,
                body: urlencoded,
                redirect: "follow",
              };

              fetch("http://localhost/submitsignup", requestOptions).then(
                (response) => {
                  if (response.status == 200) {
                    console.log("OK");
                    setText("");
                    props.swap(true);
                  } else {
                    setText("Account Create Failed !");
                    console.log("Failed catch");
                  }
                }
              );
            }}
            sx={{ width: "30vw" }}
            variant="contained"
          >
            Create Account
          </Button>
        </Grid>

        <Grid item xs={6}>
          <Button
            onClick={() => {
              props.swap(true);
            }}
            sx={{ width: "30vw" }}
          >
            Have an account ? Log in
          </Button>
        </Grid>

        <Grid item xs={6}>
          <Typography color="error">{text}</Typography>
        </Grid>
      </Grid>
    </div>
  );
}

export default CreateAccount;
