import { PropaneSharp } from "@mui/icons-material";
import { Button, Grid, Stack, TextField, Typography } from "@mui/material";
import { useState } from "react";

function Login(props) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [text, setText] = useState("");
  return (
    <div>
      <Grid
        container
        spacing={2}
        direction="column"
        alignItems="center"
        justifyContent="center"
        style={{ minHeight: "69vh" }}
      >
        <Grid item xs={6}>
          <TextField
            label="Email Address"
            onChange={(event) => {
              setEmail(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            onChange={(event) => {
              setPassword(event.currentTarget.value);
              console.error(event.currentTarget.value);
            }}
            label="Password"
            type="password"
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <Button
            onClick={() => {
              if (email == "admin" && password == "admin") {
                //admin mode
                console.log("admin");
                setText("");
                props.auth(true);
                props.admin(true);
                props.goback(false);
              } else {
                var myHeaders = new Headers();
                myHeaders.append(
                  "Content-Type",
                  "application/x-www-form-urlencoded"
                );

                var urlencoded = new URLSearchParams();
                urlencoded.append("email", email);
                urlencoded.append("pass", password);

                var requestOptions = {
                  method: "POST",
                  headers: myHeaders,
                  body: urlencoded,
                  redirect: "follow",
                };

                fetch("http://localhost/login", requestOptions).then(
                  (response) => {
                    if (response.status == 200) {
                      console.log("OK");
                      setText("");
                      props.auth(true);
                      props.goback(false);
                    } else {
                      setText("Login Failed !");
                      console.log("Failed catch");
                    }
                  }
                );
              }
            }}
            sx={{ width: "30vw" }}
            variant="contained"
          >
            Log In
          </Button>
        </Grid>
        <Grid item xs={6}>
          <Button
            onClick={() => {
              props.swap(false);
            }}
            sx={{ width: "30vw" }}
          >
            New Here ? Create an account
          </Button>
        </Grid>
        <Grid item xs={6}>
          <Typography color="error">{text}</Typography>
        </Grid>
      </Grid>
    </div>
  );
}

export default Login;
