import {
  Button,
  Grid,
  Select,
  TextField,
  MenuItem,
  FormControl,
} from "@mui/material";
import { useState } from "react";

function EventCreate(props) {
  const [eventno, setEventNo] = useState("");
  const [eventname, setEventName] = useState("");
  const [coordname, setCoordName] = useState("");
  const [coordno, setCoordNo] = useState("");
  const [fee, setFee] = useState("");
  const [event, setEvent] = useState("");
  const [date, setDate] = useState("");
  const [type, setType] = useState("");
  return (
    <div>
      <Grid
        container
        spacing={2}
        direction="column"
        alignItems="center"
        justifyContent="center"
      >
        <Grid item xs={6}>
          <TextField
            required
            label="Event Name"
            onChange={(event) => {
              setEventName(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            label="Event Number"
            type="number"
            onChange={(event) => {
              setEventNo(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            label="Co-Ordinator Name"
            onChange={(event) => {
              setCoordName(event.currentTarget.value);
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            onChange={(event) => {
              setCoordNo(event.currentTarget.value);
            }}
            label="Co-Ordinator Phone Number"
            type="number"
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            onChange={(event) => {
              setFee(event.currentTarget.value);
            }}
            label="Registration Fee"
            type="number"
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            onChange={(event) => {
              setEvent(event.currentTarget.value);
            }}
            label="Venue of Event"
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            required
            onChange={(event) => {
              setDate(event.currentTarget.value);
            }}
            type="date"
            label="Date of Event"
            InputLabelProps={{
              shrink: true,
            }}
            sx={{ width: "30vw" }}
          ></TextField>
        </Grid>
        <Grid item xs={6}>
          <TextField
            select
            sx={{ width: "30vw" }}
            value={type}
            label="Event Type"
            placeholder="Event Type"
            onChange={(event) => {
              setType(event.target.value);
            }}
          >
            <MenuItem value={"Model"}>Model</MenuItem>
            <MenuItem value={"Playback Singer"}>Playback Singer</MenuItem>
            <MenuItem value={"Motivational Speaker"}>
              Motivational Speaker
            </MenuItem>
          </TextField>
        </Grid>
        <Grid item xs={6}>
          <Button
            disabled={
              eventname == "" ||
              eventno == "" ||
              event == "" ||
              coordname == "" ||
              coordno == "" ||
              fee == "" ||
              date == "" ||
              type == ""
            }
            onClick={() => {
              var myHeaders = new Headers();
              myHeaders.append(
                "Content-Type",
                "application/x-www-form-urlencoded"
              );

              var urlencoded = new URLSearchParams();
              urlencoded.append("eventno", eventno);
              urlencoded.append("eventname", eventname);
              urlencoded.append("coordinatorname", coordname);
              urlencoded.append("coordinatorno", coordno);
              urlencoded.append("fee", fee);
              urlencoded.append("venue", event);
              urlencoded.append("date", date);
              urlencoded.append("type", type);

              var requestOptions = {
                method: "POST",
                headers: myHeaders,
                body: urlencoded,
                redirect: "follow",
              };

              fetch("http://localhost/addevent", requestOptions).then(
                (response) => {
                  if (response.status == 200) {
                    console.log("OK");
                    fetch("http://localhost/loadeventlist")
                      .then((response) => response.json())
                      .then((result) => {
                        props.update[0](result);
                      })
                      .catch((error) => console.log("error", error));
                    props.update[1]();
                  } else console.log("Not OK");
                }
              );
            }}
            sx={{ width: "30vw" }}
            variant="contained"
          >
            Create
          </Button>
        </Grid>
      </Grid>
    </div>
  );
}

export default EventCreate;
